.lineup {
    background-color: $rosa;
    padding: $separacion 0;

    h3,p {
        text-align: center;
        color: $negro;
    } 

    h3 {
        font-size: 3.5rem;

    }

    .dia {
        text-transform: uppercase;
        margin: $separacion 0;
        font-weight: $bold;
    }
}

.escenarios-contenedor {
    @include tablet {
        @include grid(2, $separacion);
    }

    .escenario {
        padding: $separacion / 2;
    }

    .nombre-escenario {
        font-size: 2.5rem;
        font-weight: $bold;

        &:after {
            content:'';
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
        }
    }
}

.edm .nombre-escenario {
    &:after {
        background-image: url(../img/icono_edm.png);
    }
}

.rock .nombre-escenario {
    &:after {
        background-image: url(../img/icono_rock.png);
    }
}

.calendario {
    padding: 0;
    list-style: none;
    font-size: 2rem;
    li {
        padding: $separacion / 2;
        font-weight: $bold;

        span {
            font-weight: $regular;
        }
    }
}

 .bg-verde .calendario li:nth-child(odd) {
    background-color: darken($verde, 5);

}
.bg-amarillo .calendario li:nth-child(odd) {
    background-color: darken($amarillo, 10);

}
