.boletos {
    padding: $separacion 0;
    background-color: $rosa;
    h3 {
        text-align: center;
    }
    .contenedor-boletos {
        margin-top: $separacion / 2; 
        @include grid(2, $separacion);
    }

    .plan {
        p{
            color: $negro;
            font-size: 2.5rem;
            text-align: center;
            margin-bottom: 2rem;
            font-weight: $bold;
        } 
    }
}

.pase {
    width: 100%;
    border-spacing: 0;
    thead {
        background-color: $amarillo;
        
        th {
            padding: $separacion / 4;
            text-transform: uppercase;
            font-weight: $regular;
            font-size: 2rem;
        }
 
    }
    tbody {
        background-color: $verde;

        td {
            padding: $separacion / 4;
            font-size: 2rem;
            border-bottom: 1px solid #e1e1e1 ;

        }

    }
    tfoot {
        background-color: $verde;
        td {
            padding: $separacion / 4;
            text-align: center;
            font-size: 3rem;
        }

    }
}